@import './components/variables';
@import './components/mobil';

body {
  background-color: $bgc-body;
}
@media screen and (min-width: 1200px) {
  .container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
  }
  .Notes__input {
    width: 400px;
    margin-left: auto;
    margin-right: auto;
    height: 100px;
    display: block;
  }
  .btn__save {
    margin-top: 15px;
    width: 200px;
    height: 50px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    cursor: pointer;

    font-size: 26px;
    line-height: 1.34;

    border-radius: 4px;
    border-color: $btn-add-color;

    color: $btn-add-color;
    background-color: $bgc-btn-add;
  }
  .title {
    margin-left: auto;
    margin-right: auto;
    width: 180px;
    text-align: center;
  }
  .Notes__label {
    margin-left: auto;
    margin-right: auto;
    width: 200px;
  }

  .js-menu {
    margin: 0;
    padding: 0;
  }
  .notes__item {
    width: 350px;
    height: 100%;

    margin: 0;
    padding: 0;
    margin-left: auto;
    margin-right: auto;
  }
  .btn {
    width: 70px;
    height: 30px;
    border-radius: 4px;
    border: transparent;
    color: $bgc-btn-add;
  }
  .delete {
    background-color: $btn-delete-color;
  }
  .change {
    background-color: $btn-change-color;
  }
}
